import { FunctionalComponent, h } from '@stencil/core';
import { FcDatepickerItem } from '../datepicker-item/fc-datepicker-item';
import {
  addDays,
  findNextValidDay,
  findPrevValidDay,
  getDateBetweenLimits,
  getWeekDaysInitials,
  getWeekDaysNamesFromInitials,
  getWeeks,
  isDisabledDate,
  WeekDays,
  weekStart,
} from '@utils/date-utils';

export type FcCalendarType = {
  shownDate?: Date;
  selectedDate?: Date;
  selectedRangeStart?: Date;
  selectedRangeEnd?: Date;
  currentDate?: Date;
  disabledDate?: Date[];
  highlightedDate?: Date[];
  highlightedWeekends: boolean;
  firstDayOfTheWeek?: WeekDays; // 0 = Sunday, 1 = Monday, etc.
  color?: string;
  minDate?: Date;
  maxDate?: Date;
  hasFocus?: boolean;
  onDateSelect: (e: MouseEvent, day: Date) => void;
  onChangeShownDate?: (day: Date) => void;
  onExit?: () => void;
};

export const FcCalendar: FunctionalComponent<FcCalendarType> = props => {
  const referenceDate = props.shownDate || props.selectedDate || props.selectedRangeStart || new Date();

  function onKeyDown(key: KeyboardEvent, day: Date) {
    let nextDate: Date | null = null;
    let isNext = true;
    switch (key.code) {
      case 'ArrowDown':
        nextDate = addDays(day, 7);
        isNext = true;
        break;

      case 'ArrowUp':
        nextDate = addDays(day, -7);
        isNext = false;
        break;

      case 'ArrowLeft':
        nextDate = addDays(day, -1);
        isNext = false;
        break;

      case 'ArrowRight':
        nextDate = addDays(day, 1);
        isNext = true;
        break;

      case 'Enter':
      case 'Space':
        key.stopPropagation();
        key.preventDefault();
        if (!isDisabledDate(day, props.disabledDate || [], props.minDate, props.maxDate)) {
          props.onDateSelect(null, day);
        }
        return;
      case 'Home':
        nextDate = weekStart(day);
        isNext = true;
        break;

      case 'End':
        nextDate = addDays(weekStart(day), 6);
        isNext = false;
        break;

      case 'PageUp':
        nextDate = new Date(day.getFullYear(), day.getMonth() - 1, day.getDate());
        isNext = false;
        break;
      case 'PageDown':
        nextDate = new Date(day.getFullYear(), day.getMonth() + 1, day.getDate());
        isNext = true;
        break;
      case 'Escape':
        key.stopPropagation();
        key.preventDefault();
        props.onExit();
        return;
      default:
        return;
    }

    key.stopPropagation();
    key.preventDefault();

    if (key.ctrlKey) {
      if (isNext) {
        props.onChangeShownDate(findNextValidDay(nextDate, props.disabledDate || [], props.minDate, props.maxDate));
      } else {
        props.onChangeShownDate(findPrevValidDay(nextDate, props.disabledDate || [], props.minDate, props.maxDate));
      }
    } else {
      props.onChangeShownDate(getDateBetweenLimits(nextDate, props.minDate, props.maxDate));
    }
  }

  const selectedDateString = props.selectedDate?.toDateString();
  const selectedRangeStartString = props.selectedRangeStart?.toDateString();
  const selectedRangeEndString = props.selectedRangeEnd?.toDateString();

  function isSelectedDay(day: Date) {
    const dayString = day.toDateString();

    if (selectedDateString) {
      return dayString === selectedDateString;
    }

    if (selectedRangeStartString && !props.selectedRangeEnd) {
      return dayString === selectedRangeStartString;
    }

    if (selectedRangeEndString && !props.selectedRangeStart) {
      return dayString === selectedRangeEndString;
    }

    if (selectedRangeStartString && selectedRangeEndString && selectedRangeStartString === selectedRangeEndString) {
      return dayString === selectedRangeStartString;
    }
    return false;
  }

  function isMiddleDay(day: Date) {
    return props.selectedRangeStart && props.selectedRangeEnd ? day > props.selectedRangeStart && day < props.selectedRangeEnd : false;
  }

  function isStartDay(day: Date) {
    return selectedRangeStartString && !isSelectedDay(day) ? day.toDateString() === selectedRangeStartString : false;
  }

  function isEndDay(day: Date) {
    return selectedRangeEndString && !isSelectedDay(day) ? day.toDateString() === selectedRangeEndString : false;
  }

  function isHighlighted(day: Date) {
    if (props.highlightedWeekends && (day.getDay() == 6 || day.getDay() == 0)) {
      return true;
    }
    return props.highlightedDate?.some(d => d.toDateString() === day.toDateString());
  }

  return (
    <table role="grid" class="fc-calendar">
      <thead class="fc-calendar__header-week">
        {getWeekDaysInitials(props.firstDayOfTheWeek ?? WeekDays.Monday).map(day => (
          <th scope="col" class="fc-calendar__header-week__day" aria-label={getWeekDaysNamesFromInitials(day)}>
            {day}
          </th>
        ))}
      </thead>
      <tbody class="fc-calendar__body-week w-full">
        {getWeeks(referenceDate, props.firstDayOfTheWeek ?? WeekDays.Monday, 6).map((week: Date[]) => (
          <tr class="fc-calendar__row-days">
            {week.map(day => (
              <FcDatepickerItem
                onDatepickerItemRef={el => {
                  if (el && props.hasFocus && day.toDateString() === props.shownDate.toDateString()) {
                    setTimeout(() => {
                      el.focus();
                    }, 1);
                  }
                }}
                onSelect={props.onDateSelect}
                onKeyDown={e => onKeyDown(e, day)}
                value={day}
                type={'day'}
                isFocusable={day.toDateString() === referenceDate.toDateString()}
                isDisabled={isDisabledDate(day, props.disabledDate || [], props.minDate, props.maxDate)}
                isOtherMonth={day.getMonth() !== referenceDate.getMonth()}
                isCurrent={day.toDateString() === (props.currentDate || new Date()).toDateString()}
                isSelected={isSelectedDay(day)}
                isStart={isStartDay(day)}
                isMiddle={isMiddleDay(day)}
                isEnd={isEndDay(day)}
                isHighlighted={isHighlighted(day)}
                color={props.color}
                text={day.getDate().toString()}
              ></FcDatepickerItem>
            ))}
          </tr>
        ))}
      </tbody>
    </table>
  );
};
